.song-information-wrapper {
  margin-top: 3px;
  text-align: left;
  &-inner{
    .song-name{
      margin-top:-5px;
      a{
        overflow-wrap: break-word;
        font-size: 14px;
        font-weight: 500;
        color: #000000;
        text-decoration: none;
        &:hover{
          color: purple;
      }
      }
    }
    .singer-name{
      margin-top: -20px;
      a {
          max-width: 100%;
          overflow-wrap: break-word;
          color: #575555;
          font-size: 12px;
          font-weight: 500;
          text-decoration: none; 
          &:hover{
          color: purple;
          }
      }  
    }
}
}
